.meatclubs{

&__container{
position: relative;
}
&__vertical-text{
  font-family: 'Rockwell Nova';
  position: absolute;
  z-index: 100;
  top: 160px;
  right: 0px;
  writing-mode: vertical-lr;
  text-orientation: upright;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--light-color);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

&__title-box{
  position: relative;
  margin-bottom: 33px;
}
&__title{
  margin: 0;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 55px;
  line-height: 72px;
}
&__decor{
  margin: 0;
  position: absolute;
  top: -20px;
  right: -40px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 105px;
  line-height: 138px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.1);
}
&__card-list{
  margin: 0;
  padding: 0;
  display: flex;
  gap: 43px;
}
&__card-box-img{
  border-radius: 35px 35px 0px 0px;
  height: 238px;
  background-image: url('../img/interior2.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

}
&__card-item{
  border-radius: 40px;
  border: 5px solid #CA4F45;
  box-shadow: 4px 4px 20px #851B13;
  width: 400px;
  height: 400px;
  background: #000000;
}
&__card-descr{
  margin: 0;
  padding-top: 41px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: var(--red-color);
}
&__card-adress{
  font-family: 'Rockwell Nova';
  display: block;
  text-align: center;
  font-weight: 400;
  font-size: 25px;
  line-height: 33px;
  text-transform: uppercase;
  }
}

@include hight-desktop{
.meatclubs__vertical-text{
  display: none;
}
.meatclubs__card-list{
  flex-wrap: wrap;
}
}
@include tablet-big{
.meatclubs__title{
  font-size: 15px;
  line-height: 20px;
}
.meatclubs__decor{
  font-size: 40px;
  line-height: 53px;
  }
.meatclubs__card-list {
  padding-left: 5px;
  gap: 5px;
  justify-content: center;
  overflow: hidden;
}

.meatclubs__card-box-img {
  border-radius: 7px 7px 0px 0px;
  width: 120px;
  height: 79px;
  background-image: url('../img/interior2.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.meatclubs__card-item {
  border-radius: 10px;
  border: 5px solid #CA4F45;
  box-shadow: 4px 4px 20px #851B13;
  width: 129px;
  height: 129px;
  background: #000000;
}
.meatclubs__card-descr {
  margin: 0;
  padding-top: 10px;
  font-weight: 700;
  font-size: 10px;
  line-height: 13px;
  text-align: center;
  text-decoration-line: underline;
  text-transform: uppercase;
  color: #CA4F45;
}
.meatclubs__card-adress {
  font-family: 'Rockwell Nova';
  display: block;
  font-size: 10px;
  line-height: 13px;
  text-align: center;
  text-transform: uppercase;
}
}
@include small-tablet{
.container{
  padding: 0;
  }
.meatclubs__card-hidden{
  display: none;
}
.meatclubs__card-list {
  justify-content: flex-start;
  flex-wrap: nowrap;
  }
}
