.maps{
  &__title-box{
   position: relative;
   margin-bottom: 20px;
  }
  &__title{
   margin: 0;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 55px;
  line-height: 72px;
  }
  &__text-decor{
    position: absolute;
    top: -30px;
    right: 0;
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 105px;
    line-height: 138px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.1);
  }
  &__descr{
    margin: 0;
    padding-top: 20px;
    text-align: center;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 50px;
    line-height: 66px;
    text-transform: uppercase;
  }
  &__tel{
    margin: 0;
    text-align: center;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 80px;
    line-height: 105px;
    text-transform: uppercase;
  }
  &__link-tel{
    color: inherit;
  }
  &__map{
    max-width: 1800px;
    display: block;
    margin: 0 auto;
  }
}
@include big-desktop{
.maps__descr{
  display: none;
}
.maps__tel{
  display: none;
}
}
@include tablet {
.maps__title-box{
  display: none;
}
.maps__map{
  filter: brightness(40%);
    height: 249px;
}
}
