.header{
  &__container{
    display: flex;
    justify-content: space-between;
  }
  &__right{
    padding-top: 69px;
  }

  &__sotial{
    display: flex;
  }
  &__tel{
    display: flex;
    align-items: center;
    margin-bottom: 17px;
  }
  &__tel-link{
    font-weight: 700;
    font-size: 30px;
    line-height: 40px;
    color: var(--red-color);
    margin-right: 43px;
  }

  &__item{
    &:not(:last-child){
      padding-right: 35px;
    }
  }
    &__count{
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 133px;
      border-radius: 100%;
      background-color: #CA4F45;
      font-weight: 400;
      font-size: 25px;
      line-height: 33px;
      color: var(--light-color);
      width: 38px;
      height: 38px;
    }

}
.nav {
  padding: 69px 0px 0px 174px;
  &__list{
    display: flex;
  }
  &__item{
    font-weight: 700;
    font-size: 25px;
    line-height: 33px;

    &:not(:last-child){
      margin-right: 44px;
    }
  }
  &__link{
    color: var(--light-color);

    &:hover{
      color: var(--red-color);
    }
  }

}

@include hight-desktop{
  .nav{
      padding: 40px 0px 0px 10px;
  }
  .nav__item{
    &:not(:last-child) {
        margin-right: 15px;
      }
  }
  .header__logo-img{
    width: 248px;
    height: 90px;
  }
  .header__tel-link{
    padding-right: 15px;
  }
  .header__right{
    padding-top: 40px;
  }
  .header__count{
    margin-right: 10px;
  }
  .header__tel-link{
    margin-right: 10px;
  }
}
@include tablet{
  .header__container{
    flex-direction: column;
    align-items: center;
  }
  .title-hidden{
    display: block;
  }
  .header__logo{
    padding-top: 96px;
  }
  .nav{
    display: none;
  }
  .header__card{
    display: none;
  }
  .header__count{
    display: none;
  }
  .header__sotial{
    display: none;
  }
  .header__right{
    padding-top: 0px;
  }
}

@include  mobile{
  .title-hidden{
    font-size: 50px;
    max-width: 320px;
  }
  .header__tel-link{
    padding-left: 10px;
    font-weight: 700;
    font-size: 15px;
    line-height: 20px;
    text-decoration-line: underline;
  }
}
