.tabs {

&__content{
display: flex;
justify-content: center;
}
&__nav-item{
cursor: pointer;
}
&__panel--active {
display: block;
}

&__nav {
display: flex;
justify-content: center;
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-align: center;
text-transform: uppercase;
margin-bottom: 77px;


&-btn {
background: transparent !important;
margin: 0px 45px 12px 0px;
font-weight: 400;
font-size: 30px;
line-height: 40px;
color: var(--light-color);
text-align: center;
text-transform: uppercase;
}
&__nav-item{
background-color: transparent !important;
}
}
&__nav-btn--active {
  background-color: transparent !important;
  font-weight: 700;
  font-size: 30px;
  line-height: 40px;
}
&__img-inner{
display: flex;
gap: 30px;
}
&__img-box{
width: 681px;
height: 511px;
img{
object-fit: cover;
width: 100%;
height: 100%;
}
}
&__panoramma-box{
margin: 0 auto;
}
&__img{
object-fit: cover;
width: 100%;
height: 100%;
}
}

@include hight-desktop{
.tabs{
display: block;
}
.tabs__img-inner{
flex-direction: column;
align-items: center;
}
.tabs__content{
display: block;
padding-bottom: 29px;
}
}
@include tablet{
.tabs__nav{
display: flex;
flex-direction: column;
align-items: center;
}
}
@include small-tablet{
.tabs__img-box {
width: 468px;
height: 297px;
}
.tabs__panoramma-box{
width: 468px;
height: 297px;
}
}

@include mobile{
.tabs__nav{
margin-bottom: 30px;
}
.tabs__nav-btn{
margin: 0;
font-size: 20px;
line-height: 45px;

&--active{
  font-size: 20px;
  line-height: 20px;
}
}
.tabs__img-box {
width: 320px;
height: 197px;
}
.tabs__panoramma-box{
width: 320px;
height: 197px;
transform: translateX(-15px);
}
}
