.footer-menu {
  padding-top: 350px;
  background-image: url('../img/svg/Union.svg');
  background-repeat: no-repeat;
  background-position: top center;

  &__adress {
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    margin-bottom: 28px;
  }

  &__list {
    padding-top: 50px;
  }

  &__item {
    display: flex;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 25px;
    line-height: 33px;
    text-transform: uppercase;

    &:not(:last-child) {
      padding-bottom: 17px;
    }
  }

  &__item-orange {
    &::before {
      content: url('../img/svg/tube-orange.svg');
      padding-right: 24px;
    }
  }

  &__item-blue {
    &::before {
      content: url('../img/svg/tube_blue.svg');
      padding-right: 24px;
    }
  }

  &__item-red {
    &::before {
      content: url('../img/svg/tube_red.svg');
      padding-right: 24px;
    }
  }

  &__item-green {
    &::before {
      content: url('../img/svg/tube_green.svg');
      padding-right: 24px;
    }
  }

  &__sotial {
    display: flex;
    justify-content: center;
    margin-bottom: 28px;
  }

  &__sotial-item {
    &:not(:last-child) {
      margin-right: 60px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;
  }

  &__descr {
    margin: 0;
    font-family: 'Rockwell Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;

    span {
      display: block;
    }
  }
  &__tel {
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);

  }

  &__year {
    padding-top: 30px;
    padding-right: 90px;
  }

  &__store {
    margin: 54px 0px 84px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__sign{
    display: none;
  }
}

@include tablet{
.footer-menu{
background-image: none;
padding-top: 50px;
}
.footer-menu__sign {
  display: block;
}
.footer-menu__hidden{
  display: none;
}

.footer-menu__descr {
  font-weight: 700;
  font-size: 10px !important;
  line-height: 13px;
}

.footer-menu__year {
  padding-top: 0;
}
}
