.catalog{
&__container{
background-image: url('../img/svg/bg_decor-right.svg');
background-position: top 500px right 100px;
background-repeat: no-repeat;
}
&__tabs-content{
display: flex;
flex-direction: column;
align-items: center;
}
&__panel{
 margin: 0 auto;
 padding-top: 50px;
}
&__nav{
border-radius: 30px;
padding: 30px 15px;
margin: 0 auto;
justify-content: start;
max-height: 100px;
max-width: 1540px;
background: #851B13;
}

&__text-box{
position: relative;
}
&__text{
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 55px;
line-height: 72px;
}
&__text-decor{
position: absolute;
top: -20px;
right: 0;
margin: 0;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 105px;
line-height: 138px;
text-transform: uppercase;
color: rgba(255, 255, 255, 0.1);
}
&__tabs{
position: relative;
margin: 0 auto;
}
&__category{
margin-right: 72px;
font-family: 'Rockwell Nova';
font-weight: 300;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;
}
&__all{
display: none;
position: absolute;
padding-top: 30px;
right:0px;
top: 90px;
border-radius: 30px;
font-family: 'Rockwell Nova';
width: 493px;
background: #851B13;
font-weight: 700;
font-size: 30px;
line-height: 40px;
color: var(--red-color);
}

.open{
display: block;
}
&__menu-btn{
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 30px;
line-height: 40px;
color: var(--red-color);

&::after{
content:url('../img/svg/list.svg');
padding: 15px 0px 0px 15px;
}
}
&__item{
padding:0px 10px 22px 10px;
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;
}
&__card-list{
max-width: 1531px;
display: grid;
grid-template-columns: repeat(3, 1fr);
gap: 53px;
}
&__card-item{
  overflow: hidden;
border-radius: 20px;
background-color: #000;
width: 475px;
height: 474px;
filter: drop-shadow(0px 8px 20px rgba(168, 41, 31, 0.5));
}
&__card-price-box{
display: flex;
padding: 18px 23px 9px;
}
&__price{
margin: 0;
padding-right: 15px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 25px;
line-height: 33px;
}
&__old-price{
margin: 0;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 25px;
line-height: 33px;
text-decoration-line: line-through;
color: #92635F;
}
&__card-descr{
font-family: 'Rockwell Nova';
padding: 0px 23px 5px 23px;
font-weight: 400;
font-size: 18px;
line-height: 24px;
}
&__btn{
display: flex;
justify-content: center;
align-items: center;
margin: 10px 0px 0px 30px;
border-radius: 20px;
width: 396px;
height: 72px;
background: #A8291F;
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;
color: var(--light-color);
}
}

@include hight-desktop{
.catalog__nav{
flex-direction: column;
align-items: center;
}
.catalog__card-list{
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.catalog__all{
position: absolute;
padding-top: 30px;
top: 500px;
left: 0;
}
.catalog__nav{
max-height: 450px;
}
}
@include tablet{
.catalog__container{
  background-image: none;
}
.catalog__text{
font-weight: 700;
font-size: 15px;
line-height: 20px;
}
.catalog__text-decor{
font-weight: 700;
font-size: 34px;
line-height: 53px;
}

.catalog__category{
font-weight: 700;
font-size: 15px;
line-height: 20px;
}

.catalog__price{
display: flex;
justify-content: center;
align-items: center;
margin-top: -15px;
border-radius: 20px;
background: #A8291F;
padding: 4px;
width: 134px;
height: 26px;
font-weight: 700;
font-size: 15px;
line-height: 20px;
}
.catalog__card-item{
display: flex;
flex-direction: column;
width: 148px;
max-height: 162px;
}
.catalog__card-box-img{
  order: -2;
}
.catalog__card-descr{
order:-1;
max-width: 100px;
padding: 4px 4px 4px 25px;
font-weight: 400;
font-size: 10px;
line-height: 13px;
}
.catalog__old-price{
  display: none;
}
.catalog__btn{
  display: none;
}
.catalog__card-list {
grid-template-columns: repeat(4, 1fr);
gap: 20px;
}
}
@include mobile{
.catalog__nav {
padding-top: 20px;
max-height: 350px;
}

}

@include small-tablet{
.catalog__card-list {
grid-template-columns: repeat(2, 1fr);
gap: 20px;
}
}
