
.graph-modal__content{
padding: 10px;
display: flex;
flex-direction: column;
align-items: center;
}
.graph-modal-open {
border-radius: 10px;
max-width: 1707px;
max-height: 1153px;
background: linear-gradient(180deg, #851B13 0%, #000000 100%);

}
.modal{

&__img-box{
margin: 0 auto;
padding-bottom: 49px;
padding-top: 52px;
border-radius: 10px;
max-width: 1428px;
}
&__img{
object-fit: cover;
width: 100%;
height: 100%;
}
&__inner{
display: flex;
justify-content: space-between;
margin-bottom: 150px;
}
&__title{
margin: 0;
padding: 0px 0px 56px 0px;
max-width: 599px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;
color: var(--light-color);
}
&__descr{
margin: 0;
padding-left: 0px;
max-width: 879px;
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 30px;
line-height: 40px;
color: var(--light-color);
}
&__left{
padding-right: 124px;
}

&__right{
display: flex;
justify-content: space-between;
min-width: 400px;
}
&__prise{
display: block;
margin: 0;
padding-bottom: 89px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 35px;
line-height: 46px;
color: var(--light-color);
}
&__prise-old{
display: block;
margin: 0;
padding-bottom: 89px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 35px;
line-height: 46px;
text-decoration-line: line-through;
color: #92635F;

}
&__item{
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 25px;
line-height: 33px;
color: var(--light-color);

&-bold{
font-weight: 700;
padding-bottom: 12px !important;
}

&:not(:last-child){
padding-bottom: 8px;
}
}
&__btn{
display: flex;
justify-content: center;
align-items: center;
border-radius: 20px;
width: 490px;
height: 72px;
background: #A8291F;
color: var(--light-color);
font-family: 'Rockwell Nova';
font-style: normal;
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;

}
}
.count {
display: flex;
margin-left: 64px;
}

.count .change {
display: flex;
justify-content: center;
align-items: center;
width: 25px;
height: 25px;
font-weight: 600;
color: var(--light-color);
background-color: transparent;
border-radius: 3px;
cursor: pointer;
}

.count input {
max-width: 55px;
text-align: center;
font-weight: 700;
color: var(--light-color);
background-color: transparent;
border: none;
border-radius: 3px;
margin: 0 5px;
}

.count .minus.min {
background-color: transparent;
}
.plus-text{
padding-top: 10px;
}

@include tablet-big{
.graph-modal__container{
padding: 5px 5px 30px 5px;
width: 380px;

}
.graph-modal__content {
padding-top: 40px;
display: block;
}

.modal__img-box{
padding: 0;
width: 358px;
height: 218px;
}
.modal__title{
margin: 0;
padding: 27px 0px;
max-width: 342px;
font-weight: 700;
font-size: 26px;
line-height: 30px;
}
.modal__descr{
padding-bottom: 6px;
max-width: 392px;
font-weight: 400;
font-size: 15px;
line-height: 16px;
}
.modal__prise{
position: absolute;
top: 10px;
left: 10px;
font-weight: 700;
font-size: 25px;
line-height: 33px;
}
.modal__prise-old {
position: absolute;
top: 10px;
left: 90px;
font-weight: 700;
font-size: 25px;
line-height: 33px;
}
.modal__inner{
display: block;
margin-bottom: 15px;
}
.modal__list{

margin-right: 40px;

}
.modal__item{
font-weight: 400;
font-size: 15px;
line-height: 20px;
}
.modal__right{
display: flex;
justify-content: start;
min-width: 300px;
}
.modal__btn{
max-width: 302px;
height: 44px;
font-weight: 700;
font-size: 20px;
line-height: 26px;

}
.plus-text {
padding-top: 0px;
}
}
