html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
  background: linear-gradient(180deg, #340000 34.78%, #A8291F 120.64%); // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  font-family: var(--font-family);
  color: var(--light-color);
}
.box-hidden {
  display: none;

  &__list {
    margin: 0 auto;
    max-width: 370px;
    padding-top: 20px;
  }

  &__item {
    margin: 0 auto;
    max-width: 70px;
    max-height: 70px;
    background: #851B13;
    border: 1px solid #CA4F45;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.25);
    border-radius: 10px;

    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__link {
    display: block;
  }

  &__img-box {
    margin-left: 10px;
    width: 36px;
    height: 36px;
  }
  &__img{
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__text {
    margin: 0;
    padding: 9px 0px 5px 0px;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 10px;
    line-height: 13px;
    text-transform: uppercase;
    color: #CA4F45;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}
.title-hidden{
  display: none;
  margin: 0;
  position: absolute;
  top: -15px;
  left: 0;
  max-width: 368px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 60px;
  line-height: 45px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.1);
}
.descr-hidden{
  display: none;
  position:absolute;
  right: -60px;
  top: -60px;
  max-width: 411px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 40px;
  line-height: 53px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.1);
}
