.modal-adress .graph-modal-open {
  padding: 0;
  max-width: 100%;
  max-height: 100%;
  background: linear-gradient(180deg, #851B13 0%, #000000 100%);
}
.modal-adress{
&__content {
  padding-bottom: 50px;
}
&__list-box{
  margin-top: 113px;
}
&__item{
display: flex;
justify-content: left;
align-items: center;
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 20px;
line-height: 26px;
text-transform: uppercase;
color: var(--light-color);
padding-bottom: 11px;
}
&__item-orange {
  &::before {
    content: url('../img/svg/tube-orange.svg');
    padding-top: 10px !important;
    padding-right: 5px;
  }
}
&__item-blue {
  &::before {
    content: url('../img/svg/tube_blue.svg');
    padding-top: 10px !important;
    padding-right: 5px;
  }
}
&__item-red {
  &::before {
    content: url('../img/svg/tube_red.svg');
    padding-top: 10px !important;
    padding-right: 5px;
  }
}

&__item-green {
  &::before {
    content: url('../img/svg/tube_green.svg');
    padding-top: 10px !important;
    padding-right: 5px;
  }
}
}

