.hero{
  &__container{
    position: relative;
  }
  &__title{
    margin: 0;
    padding-bottom: 55px;
    background-image: url('../img/svg/title_text-bg.svg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: 40px;
    font-family: 'Rockwell Nova';
    padding-left: 137px;
    margin: 0;
    font-weight: 700;
    font-size: 80px;
    line-height: 105px;
    color: var(--light-color);
  }
  &__bottom{
    display: flex;
    margin-bottom: 78px;
  }
  &__store{
    display: flex;
    align-items: center;
  }
  &__descr{
    margin: 0;
    padding-left: 126px;
    font-weight: 700;
    font-size: 30px;
    line-height: 40px;
    text-transform: uppercase;
    color:var(--light-color);
  }
  &__phone{
    margin-right: 589px;
  }
  &__tel-link{
    padding-left: 126px;
    font-weight: 700;
    font-size: 50px;
    line-height: 66px;
    text-transform: uppercase;
    color: var(--light-color);
  }
  &__appstore-img{
    padding-right: 42px;
  }
  &__vertical-text{
    position: absolute;
    top: 100px;
    right: 100px;
    writing-mode: vertical-lr;
    text-orientation: upright;
    font-family: 'Rockwell Nova';
    font-style: normal;
    font-weight: 400;
    font-size: 35px;
    line-height: 46px;
    text-transform: uppercase;
    color: var(--light-color);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    cursor:pointer;
  }
}
@include hight-desktop{
  .hero__title{
    font-size: 60px;
    padding-left: 60px;
  }
  .hero__vertical-text{
    font-size: 20px;
    line-height: 26px;
  }
  .hero__phone{
    margin-right: 200px;
  }
  .hero__tel-link{
    padding-left: 60px;
  }
  .hero__descr{
    padding-left: 60px;
  }
}
@include big-desktop{
  .hero__title{
    font-size: 40px;
  }
}

@include tablet{
  .hero__bg{
    display: none;
  }
 .hero__bottom{
  display: none;
 }
  .hero__vertical-text{
    display: none;
  }
  .hero__title{
    background-position-y: 0px;
    padding-left: 20px;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
  }
}
@include tablet{
  .descr-hidden{
    display: block;
  }
}
