.footer-delivery {
  padding-top:0;
  background-image: url('../img/svg/head_goat.svg');
  background-repeat: no-repeat;
  background-position: top center;

&__adress {
  padding-top: 30px;
  display: flex;
  justify-content: space-around;
  margin-bottom: 28px;
}
&__hidden {
  display: none !important;
  padding-top: 30px;
  display: flex;
  justify-content: space-around;
  margin-bottom: 28px;
}

&__list {
  padding-top: 50px;
}

&__item {
  display: flex;
  align-items: center;
  font-family: 'Rockwell Nova';
  font-weight: 400;
  font-size: 25px;
  line-height: 33px;
  text-transform: uppercase;

  &:not(:last-child) {
    padding-bottom: 17px;
  }
}

&__item-orange {
  &::before {
    content: url('../img/svg/tube-orange.svg');
    padding-right: 24px;
  }
}

&__item-blue {
  &::before {
    content: url('../img/svg/tube_blue.svg');
    padding-right: 24px;
  }
}

&__item-red {
  &::before {
    content: url('../img/svg/tube_red.svg');
    padding-right: 24px;
  }
}

&__item-green {
  &::before {
    content: url('../img/svg/tube_green.svg');
    padding-right: 24px;
  }
}

&__sotial {
  display: flex;
  justify-content: center;
  margin-bottom: 28px;
  gap: 60px;
}

&__bottom {
  display: flex;
  justify-content: space-between;
}

&__descr {
  margin: 0;
  font-family: 'Rockwell Nova';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;

  span {
    display: block;
  }
}

&__tel {
  display: none !important;
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  color: var(--light-color);
}

&__year {
  padding-top: 30px;
  padding-right: 90px;
}
&__store {
  margin: 54px 0px 84px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
&__tel{
  display: flex;
  justify-content: center;
}
&__link-tel{
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
  text-decoration-line: underline;
  color: #CA4F45;
}
}

@include big-desktop{
.footer-delivery{
  background-image: none;
  padding-top: 0;
}
.footer-delivery__adress{
 display: none;
}
.footer-delivery__hidden{
  padding: 0;
  display: flex !important;
  justify-content: space-around;
}
.footer__list{
  padding-top: 20px;
}
.footer-delivery__item{
  font-weight: 400;
  font-size: 9px;
  line-height: 13px;
  text-transform: uppercase;
  padding-bottom: 0 !important;
}
.footer-delivery__item{
&::before {
 padding: 0px 5px 0px 0px;
}
}
.footer-delivery__store{
  margin: 30px 0px;
}
.footer-delivery__app{
  &:nth-child(1){
  width: 49px;
  height: 49px;
  }
  &:nth-child(2) {
  width: 69px;
  height: 69px;
  }
  }
.footer-delivery__sotial{
  gap: 30px;
}
.footer-delivery__tel{
  display: block !important;
}
}


