.menu{
&__container{
background-repeat: no-repeat;
}
&__title{
margin: 0 auto;
max-width: 896px;
font-style: normal;
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-align: center;
padding-bottom: 99px;
}
&__title-big{
margin: 0 auto;
max-width: 1196px;
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-align: center;
margin-bottom: 15px;
}
&__subtitle{
margin: 0;
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-align: center;
text-transform: uppercase;
margin-bottom: 44px;
}
&__descr-box{
position: relative;
display: flex;
justify-content: space-between;
align-items: center;
margin-bottom: 10px;
padding: 64px 0px 32px 0px;
}
&__descr{
margin: 0;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 55px;
line-height: 72px;
}
&__decor{
margin: 0;
position: absolute;
top: 25px;
right: -20px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 105px;
line-height: 138px;
text-transform: uppercase;
color: rgba(255, 255, 255, 0.1);
}
&__card-list {
display: none !important;
display: flex;
justify-content: center;
overflow: hidden;
}
&__card-box-img {
border-radius: 7px 7px 0px 0px;
width: 130px;
height: 89px;
background-image: url('../img/interior2.jpg');
background-size: cover;
background-repeat: no-repeat;
background-position: center;
}

&__card-item {
border-radius: 10px;
border: 5px solid #CA4F45;
box-shadow: 4px 4px 20px #851B13;
width: 139px;
height: 139px;
background: #000000;

    &:not(:last-child) {
      margin-right: 7px;
    }
  }

&__card-descr {
margin: 0;
padding-top: 10px;
font-weight: 700;
font-size: 10px;
line-height: 13px;
text-align: center;
text-decoration-line: underline;
text-transform: uppercase;
color: #CA4F45;

}

&__card-adress {
font-family: 'Rockwell Nova';
display: block;
font-size: 10px;
line-height: 13px;
text-align: center;
text-transform: uppercase;
}
&__box-hidden{
display: none;
position: relative;

}
&__decor-hidden{
display: none;
margin: 0;
position: absolute;
max-width: 411px;
right:-20px;
top: -5px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 30px;
line-height: 53px;
text-transform: uppercase;
color: rgba(255, 255, 255, 0.1);
  }
&__title-novisible{
padding-bottom: 10px;
font-family: 'Rockwell Nova';
font-style: normal;
font-weight: 700;
font-size: 15px;
line-height: 20px;
}
&__bottom{
  display: none;
  margin-top: 104px;
  position: relative;
}
&__bottom-descr{
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 15px;
  line-height: 20px;
}
&__bottom-decor{
  position: absolute;
  top: -100px;
  right: -20px;
  max-width: 371px;
  font-family: 'Rockwell Nova';
  font-weight: 700;
  font-size: 50px;
  line-height: 40px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.1);

}
&__map{
  max-width: 1800px;
}
}

.menu__map{
display: block;
margin: 0 auto;
}

@include hight-desktop{
.menu__decor{
font-size: 70px;
}
}
@include big-desktop{
.tabs__nav-btn{
margin-right: 20px;
}
}
@include tablet-big{
.menu__decor{
font-size: 30px;
}
.menu__box-hidden{
display: flex;
justify-content: space-between;
}
.menu__card-list{
display: flex !important;
}
.menu__decor-hidden{
display: block;
}
.menu__descr {
display: block;
font-size: 15px;
}
}
@include tablet{
.menu__container{
display: flex;
flex-direction: column;
background-image: none !important;
}
.menu__tabs-item{
display: none;
}
.tabs{
order: 0;
}
.menu__title{
max-width: 380px;
padding-bottom: 26px;
font-weight: 400;
font-size: 15px;
line-height: 20px;
text-align: center;
order: 1;
}
.menu__title-hidden{
order: 2;
}

.menu__card-hidden{
order: 3;
}
.menu__box-hidden{
order: 4;
}
.menu__card-list{
order: 5;
}
.menu__descr-box{
order: 6;
}
.menu__decor{
order: 7;
}
.menu__map{
order: 8;
filter: brightness(40%);
height: 249px;
}
.menu__bottom{
display: block;
order: 9;
}

}
@include mobile{
.menu__card-list {
display: flex;
justify-content: start !important;
}

}


