.bakestock{
  padding: 0px 10px;

  &__title-box{
   position: relative;
    margin-bottom: 67px;
  }
  &__title{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 55px;
    line-height: 72px;
  }
  &__decor-text{
    position: absolute;
    top: -20px;
    right: 0;
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 105px;
    line-height: 138px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.1);
  }
  &__img-box{
    margin-bottom: 9px;
  }
  &__img{
    object-fit: cover;
    width: 100%;
    height: 100%;

  }
  &__img-small{
    display: none;
    margin: 0 auto;
  }
  &__descr{
    margin: 0;
    max-width: 1541px;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 45px;
    line-height: 59px;
  }
  &__title-bottom{
    margin-top: 49px;
  }
}
@include big-desktop{
.bakestock__decor-text{
top: 0;
font-weight: 700;
font-size: 40px;
line-height: 53px;
}
}

@include small-tablet{
.bakestock__img{
display: none;
}
.bakestock__img-small{
display: block;
}
.bakestock__title{
font-weight: 700;
font-size: 20px;
line-height: 26px;
}
.bakestock__decor-text{
top: -20px;
}
.bakestock__descr{
margin: 0 auto;
max-width: 351px;
font-weight: 400;
font-size: 15px;
line-height: 20px;
}
.bakestock__title-bottom{
  display: none;
}
}
