.footer{
  padding-top: 120px;
  background-image: url('../img/svg/head_goat.svg');
  background-repeat: no-repeat;
  background-position: top -100px  center;

  &__hidden{
    display: none !important;
    display: flex;
    justify-content: center;
  }
   &__slide{
    display: flex;
    padding-bottom: 30px;
  }
  &__slide-box{
    position: relative;
    width: 144px;
    height: 144px;
    background: #000000;
    box-shadow: 0px 4px 50px #A8291F;
    margin-right: 25px;
    margin-bottom: 25px;
  }
  &__img-box{
    position: absolute;
    top: 15px;
    left: 15px;
    width: 141px;
    height: 141px;
    background-image: url('../img/stock_bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__text{
    margin: 0;
    padding-top: 10px;
    max-width: 144px;
    font-family: 'Rockwell Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-transform: uppercase;

  }
  &__text-sub{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;

  }
  &__descr{
    margin: 0;
    max-width: 169px;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;

  }
   &__slide-text{
    position: relative;
  }
  &__link{
    position: absolute;
    bottom: 0px;
    left: 100px;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 10px;
    line-height: 13px;
    color: var(--light-color);
  }

  &__adress{
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    margin-bottom: 28px;
  }
  &__list{
    margin-top: 10px;
    padding-top: 50px;
  }
  &__item{
    display: flex;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 25px;
    line-height: 33px;
    text-transform: uppercase;
    &:not(:last-child){
      padding-bottom: 17px;
    }
  }
    &__item-orange {
       &::before {
         content: url('../img/svg/tube-orange.svg');
         padding-right: 24px;
       }
     }
      &__item-blue {
       &::before {
         content: url('../img/svg/tube_blue.svg');
          padding-right: 24px;
       }
     }
      &__item-red {
       &::before {
         content: url('../img/svg/tube_red.svg');
          padding-right: 24px;
       }
     }
      &__item-green {
       &::before {
         content: url('../img/svg/tube_green.svg');
          padding-right: 24px;
       }
     }
   &__store{
    display: none;

   }
   &__app{
    width: 41px;
    height: 41px;
   }
   &__store-box{
    width: 69px;
    height: 69px;
   }

   &__img{
    object-fit:contain;
    width: 100%;
    height: 100%;
   }
   &__sotial{
    display: flex;
    justify-content: center;
    margin-bottom: 28px;
    padding: 16px 0px 31px 0px;
   }
   &__sotial-item{
    &:not(:last-child){
      margin-right: 30px;
    }
   }
   &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 50px;
   }
   &__descr{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;

    span{
      display: block;
    }
   }
   &__tel{
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    color: var(--light-color);
   }
   &__tel-hidden{
    display: none;
    padding-bottom: 44px;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    text-decoration-line: underline;
    color: #CA4F45;
   }
   &__year{
    padding-top: 30px;
    padding-right: 90px;
   }
   &__sign{
    display: none;
   }
}
@include big-desktop{
  .footer__item{
    font-size:20px;
  }
}
@include tablet{
  .footer{
    padding-top: 60px;
    background-image: none;
  }
  .footer__hidden{
    display: flex !important;
  }
  .footer__adress{
    display: none;
  }
  .footer__store{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer__tel-hidden{
    display: flex !important;
    justify-content: center;
  }
  .footer__descr-hidden{
    display: none;
  }
  .footer__sign{
    display: block;
  }
  .footer__descr{
    font-weight: 700;
    font-size: 10px !important;
    line-height: 13px;
  }
  .footer__year{
    padding-top: 0;
  }
}
@include mobile-small{
  .footer__slide{
    flex-direction: column;
    align-items: center;
  }
}

