.stock{
   &__container{
    background-image: url('../img/menu_decor.png');
    background-position: left 200px;
    background-repeat: no-repeat;
    padding-bottom: 387px;
  }

  &__title-box{
    position: relative;
    margin: 70px 0px 77px 0px;
  }
  &__title{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 55px;
    line-height: 72px;
  }
  &__title-decor{
    position: absolute;
    top: -20px;
    right: 0;
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 105px;
    line-height: 138px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.1);
  }
  &__decor-hidden{
    display: none;
    position: absolute;
    top: -40px;
    right: 0;
    margin: 0;
    text-align: left;
    max-width: 371px;
    height: 80px;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 50px;
    line-height: 40px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.1);
    }

  &__slide{
    display: flex;
    padding: 0px 40px 100px 0px;
  }
  &__slide-box{
    position: relative;
    width: 385px;
    height: 385px;
    background: #000000;
    box-shadow: 0px 4px 50px #A8291F;
    margin-right: 58px;
  }
  &__img-box{
    position: absolute;
    top: 40px;
    left: 25px;
    width: 377px;
    height: 377px;
    background-image: url('../img/stock_bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  &__text{
    margin: 0;
    padding-top: 20px;
    max-width: 354px;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 40px;
    line-height: 53px;
    text-transform: uppercase;
  }
  &__text-sub{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 300;
    font-size: 40px;
    line-height: 53px;
  }
  &__descr{
    margin: 0;
    max-width: 249px;
    font-family: 'Rockwell Nova';
    font-weight: 300;
    font-size: 30px;
    line-height: 40px;
  }
  &__slide-text{
    position: relative;
  }
  &__link{
    position: absolute;
    bottom: -50px;
    left: 150px;
    font-family: 'Rockwell Nova';
    font-weight: 400;
    font-size: 30px;
    line-height: 40px;
    color: var(--light-color);
  }
  &__prev{
    visibility: hidden;
  }
  &__next{
    &::before,
    &::after{
      visibility: hidden;
    }
    z-index: 100;
    width: 376px;
    background-image: url('../img/svg/arrow.svg');
    background-size: contain;
    background-repeat: no-repeat;
  }
  &__main-title{
    margin: 0;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 300px;
    line-height: 395px;
    text-align: center;
  }
  &__main-descr{
    margin: 0;
    margin-top: -120px;
    padding-left: 450px;
    padding-bottom: 51px;
    font-family: 'Rockwell Nova';
    font-weight: 700;
    font-size: 70px;
    line-height: 92px;
    text-align: center;
  }
  &__btn-link{
    display: block;
    margin: 0 auto;
    max-width: 474px;
    height: 100px;
    padding: 20px 10px;
    background: #851B13;
    border: 4px solid #CA4F45;
    box-shadow: 0px 4px 24px #A8291F;
    border-radius: 60px;
    font-family: 'Rockwell Nova';
    font-weight: 300;
    font-size: 40px;
    line-height: 53px;
    text-align: center;
    color: var(--light-color);
  }
}
@include most-hight{
.stock__next{
  background-image: none;
  }
}
@include hight-desktop{
.stock__slide{
    padding-right: 10px;
  }
.stock__main-title{
  font-size: 150px;
}
.stock__main-descr{
  font-size: 40px;
  padding-left: 0px;
}
}
@include big-desktop{
.stock__title-decor{
  display: none;
}
.stock__decor-hidden{
  display: block;
}
.stock__title{
  font-size: 15px;
  line-height: 20px;
}
}
@include tablet{
.stock__container{
  padding-bottom: 18px;
}
}
@include tablet-big{
.stock__container{
  background-image: none;
}
.stock__container-hidden{
  background-image: url('../img/meatclubs_bg.svg');
  background-position: top 150px center;
  background-repeat: no-repeat;
}
.stock__slider {
  max-width: 800px;
}
.stock__slide {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.stock__slide-box {
  position: relative;
  width: 144px;
  height: 144px;
  background: #000000;
  box-shadow: 0px 4px 50px #A8291F;
  margin-right: 25px;
  margin-bottom: 25px;
}

.stock__img-box {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 141px;
  height: 141px;
  background-image: url('../img/stock_bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.stock__text {
  margin: 0;
  padding-top: 10px;
  max-width: 144px;
  font-family: 'Rockwell Nova';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
  text-transform: uppercase;
}

.stock__text-sub {
  margin: 0;
  font-family: 'Rockwell Nova';
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
}

.stock__descr {
  margin: 0;
  max-width: 169px;
  font-family: 'Rockwell Nova';
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
}

.stock__slide-text {
  position: relative;
}
.stock__link {
  position: absolute;
  bottom: 0px;
  left: 100px;
  font-family: 'Rockwell Nova';
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: var(--light-color);
}
.stock__main-title {
  display: none;
}
.stock__main-descr {
 display: none;
}
.stock__btn-link{
  display: none;
}
.box-hidden{
  display: block !important;
}
}
@include mobile-small {
  .stock__slide {
    flex-direction: column;
    align-items: center;
  }
}

