/* stylelint-disable */
@mixin most-hight {
  @media (max-width: (2100px)) {
    @content;
  }
}
@mixin hight-desktop {
  @media (max-width: (1900px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}
@mixin tablet-big {
  @media (max-width: (992px)) {
    @content;
  }
}


@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin mobile-small {
  @media (max-width: (370px)) {
    @content;
  }
}
