.cart{
padding: 0px 10px;
&__title-box{
position:relative;
padding-bottom: 76px;
}
&__title{
margin: 0;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 55px;
line-height: 72px;
color: var(--light-color);
}
&__title-decor{
margin: 0;
position: absolute;
top: -20px;
right: 0;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 105px;
line-height: 138px;
text-transform: uppercase;
color: rgba(255, 255, 255, 0.1);
}
&__link-back{
display: block;
font-family: 'Rockwell Nova';
margin-bottom: 66px;
font-weight: 700;
font-size: 30px;
line-height: 40px;
color:var(--light-color);

&::before{
  content: url('../img/svg/arrow_back.svg');
  padding-right: 10px;
}
}

&__form{
display: flex;
}
&__form-input{
max-width: 1679px !important;
}

&__input-adress{
width: 100%;
border: none;
padding: 16px;
margin-bottom: 39px;
background: rgba(0, 0, 0, 0.1);
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 25px;
line-height: 33px;
color: var(--light-color) !important;

&::placeholder{

  color: var(--light-color);
}
}
&__input{
border: none;
padding: 16px;
width: 247px;
background: rgba(0, 0, 0, 0.1);
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 25px;
line-height: 33px;
color: var(--light-color) !important;

&-home{
  width: 189px;
}
&-tel{
  width: 625px;
}

&::placeholder{

  color: var(--light-color);
}
&:not(:last-child){
  margin-right: 40px;
}
}
&__total{
margin-left: 89px;
width: 491px;
height: 380px;
background: rgba(0, 0, 0, 0.25);
box-shadow: 0px 4px 12px rgba(168, 41, 31, 0.6);
border-radius: 10px;
  }
&__total-title{
margin: 0;
padding: 10px 10px 20px 10px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 55px;
line-height: 72px;
text-transform: uppercase;
}
&__total-inner{
display: flex;
justify-content: space-between;
}

&__item{
padding: 0px 10px 20px 10px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 25px;
line-height: 33px;
color: var(--light-color);
}
&__order-list{
max-width: 1364px;
margin-bottom: 94px;
}
&__order-item{
position: relative;
border-radius: 0px 20px 20px 0px;
max-height: 155px;
display: flex;
background: rgba(0, 0, 0, 0.1);
align-items: center;
margin-bottom: 29px;
}
&__img-box{
width: 317px;
height: 145px;
}
&__img{
border-radius: 20px 0px 0px 20px;
object-fit: cover;
width: 100%;
height: 100%;
}
&__descr{
margin: 0;
padding-left: 61px;
font-family: 'Rockwell Nova';
font-weight: 400;
font-size: 30px;
line-height: 40px;
text-transform: uppercase;
}
&__count{
margin-left: 20px !important;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 40px;
line-height: 53px;
}
&__prise-old{
padding-left: 110px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 40px;
line-height: 53px;
text-decoration-line: line-through;
color: #92635F;
}
&__prise{
padding-left: 110px;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 40px;
line-height: 53px;
}
&__btn{
margin: 0 auto;
border-radius: 20px;
padding: 9px 60px;
max-width: 396px;
background: #A8291F;
font-family: 'Rockwell Nova';
font-weight: 700;
font-size: 30px;
line-height: 40px;
display: flex;
align-items: center;
text-align: center;
text-transform: uppercase;
}
&__link{
color: var(--light-color);
}
&__close{
position: absolute;
width: 25px;
height: 25px;
background-image: url('../img/svg/close.svg');
background-repeat: no-repeat;
background-position: center;
top: 10px;
right: 10px;
z-index: 10;
}
&__hidden{
  padding-bottom: 40px;
}
&__btn-hidden{
  display: none;}
}

@include big-desktop{
.cart__title-decor{
display: none;
}
.cart__form{
display: block;
.cart__total{
  margin: 40px 20px;
}
}
}
@include tablet{
.cart__form{
display: none;
}
.cart__title-box{
padding-bottom: 20px;
}
.cart__title{
font-size: 20px;
line-height: 26px
}
.cart__link-back{
font-size: 20px;
line-height: 26px
}
.cart__order-list{
position: relative;
// max-width: 449px;
}
.cart__img-box{
width: 74px;
height: 68px;
}
.cart__img{
border-radius: 10px;
}
.cart__order-list{
margin: 0 auto;
max-width: 400px;
}
.cart__order-item{
position: relative;
}
.cart__descr{
position: absolute;
top: 10px;
left: 90px;
padding-left: 5px;
padding-right: 5px;
max-width: 111px;
font-weight: 700;
font-size: 10px;
line-height: 13px;
}
.cart__count{
padding-top: 30px;
font-size: 15px;
line-height: 20px;
}
.cart__prise{
padding-left: 5px;
padding-top: 30px;
font-size: 15px;
line-height: 20px;
}
.cart__prise-old{
padding-left: 5px;
padding-top: 30px;
font-size: 15px;
line-height: 20px;
}
.cart__btn{
max-width: 214px;
font-size: 15px;
line-height: 20px;
margin-bottom: 145px;
}
.cart__btn{
display: none;
}
.cart__btn-hidden{
display: block;
}
}
